import axios from "axios";
import marked from "marked";
import _ from "loadsh";
import "../css/index.module.scss";
import "github-markdown-css";

// const json = require("../mock/menu/menu.json");

const hello = (async function hello() {
    const menu = document.querySelector("#menu");
    const list = document.querySelector("#list");
    const listContent = document.querySelector("#list-content");
    const content = document.querySelector("#content");

    // 获取菜单数据
    let host = '//docs.liushengjie.cn'
    const local = localStorage.getItem('dataMenu');
    if(!local){
        await axios.get(host+'/userData/menu/menu.json').then(function (response) {
            // handle success
            localStorage.setItem('dataMenu', JSON.stringify(response.data) );
        })
        .catch(function (error) {
            // handle error
        })
        .then(function () {
            // always executed
        });
    }

    const dataMenu = localStorage.getItem('dataMenu') 
    const json = JSON.parse(dataMenu);

    showMenu("html", json["html"]);

    menu.addEventListener("click", function (event) {
        var e = event ? event : window.event;
        var target = e.target || e.srcElement;
        // 如果点击是I标签
        if (target && target.nodeName == "LI") {
            const menu = target.getAttribute("data-menu");
            showMenu(menu, json[menu]);
        } else {
            list.setAttribute("class", "");
            content.setAttribute("class", "");
        }
    });

    list.addEventListener("click", function (event) {
        var e = event ? event : window.event;
        var target = e.target || e.srcElement;
        // 如果点击是I标签
        if (target && target.nodeName == "LI") {
            const src = target.getAttribute("data-src");
            showContent(src);
        } else {
            list.setAttribute("class", "");
            content.setAttribute("class", "");
        }
    });

    // 渲染菜单
    function showMenu(menu, data) {
        list.setAttribute("class", "");
        content.setAttribute("class", "");

        let li = "";
        data &&
            data.forEach((element) => {
                li += `<li data-src="${host}/userData/markdown/${menu}/${element}.md" >${element}</li>`;
            });
        listContent.innerHTML = `<ul>${li || "暂无内容"}</ul>`;
    }

    // 渲染内容
    function showContent(src) {
        list.setAttribute("class", "active");
        content.setAttribute("class", "active");

        content.innerHTML = "";

        axios
            .get(src)
            .then(function (response) {
                // handle success
                // 从 markdown 到 html
                content.innerHTML = marked(response.data);
            })
            .catch(function (error) {
                // handle error
            })
            .then(function () {
                // always executed
            });
    }
})();

export default hello;



